export const functions = {
    normal: '',
    userInfo: '',
    checkOperator: '',
    checkEnemy: '',
    checkMaterial: '',
    recruit: '',
    gacha: '',
    jadeCalculator: '',
    intellectAlarm: '',
    functionQuery: '',
    weibo: ''
}

export const functionsSetting = {
    globalState: (
        () => {
            const globalState = {}
            for (let id in functions) {
                globalState[id] = true
            }
            return globalState
        }
    )(),
    weiboSetting: {
        weiboSendGIF: true,
        weiboAutoPush: true,
        checkRate: 30
    },
    jadeSetting: {
        signIn: 0,
        dailyTasks: 0,
        weeklyTasks: 0,
        weeklyBattle: 0
    }
}



